<script>
import { defineComponent } from 'vue'
import { Carousel, Slide } from 'vue3-carousel'

import 'vue3-carousel/dist/carousel.css'
export default defineComponent({
  props: {
    placeholder: {},
    slider: {},
    },
    components: {
      Carousel,
      Slide,
    },
    data(){
      return {
        scrach: ''
      }
    },
    methods: {
      toggleActive(index) {
      this.slider.forEach((slide, idx) => {
        slide.active = idx === index;
      });
    }
    }
})

