<script setup>
import HeadVue from '../components/HeadVue.vue';
import NavVue from '../components/NavVue.vue';
import CardVue from '../components/CardVue.vue';
</script>
<template>
    <div class="container">
     <HeadVue
     Head = 'Кандидаты'
     Close = 'false'
     Question = 'true'
    />
     <div class="content">
        <NavVue
            placeholder = 'Искать кандидата'
            :slider = slider
        />
        <CardVue
        img= 'https://3dnews.ru/assets/external/illustrations/2020/07/06/1014967/01.jpg'
        v-for="card in 10" :key="card "
        />
        <button @click="ModalStart">test</button>
        <button @click="window.Telegram.WebApp.sendData('/start')">mess</button>
     </div>
    </div>
    <div 
    v-if="modal == true"
    class="modal_back">
        <div class="modal_container" :class="{active: (active == true)}">
            <HeadVue
            @ModalEnd="ModalEnd"
                :Head = HeadVueTitle
                Close = 'true'
                Question = 'false'
                />
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            modal: false,
            active: false,
            HeadVueTitle: 'Заголовок',
            slider: [
                { name: 'Все', count: 2, active: true },
                { name: '3D Artist', count: 1, active: false },
                { name: 'Python Developer', count: 1, active: false },
                { name: 'Python Developer', count: 1, active: false }
            ],
        }
    },
    methods: {
        ModalStart() {
            this.modal = true
            setTimeout (async() =>{
                this.active = true
            },100 )
        },
        ModalEnd() {
            this.active = false
            setTimeout (async() =>{
                this.modal = false
            },250 )
        }
    }
}
</script>

